html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video { margin:0; padding:0; border:0; font-size:10%; font-family:sans-serif; vertical-align:baseline}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {display:block}
body {line-height:1}
ol, ul {list-style:none}
blockquote, q {quotes:none}
blockquote:before, blockquote:after, q:before, q:after {content:''; content:none}
table {border-collapse:collapse; border-spacing:0}
a {color:#000; text-decoration:none}
a:focus,
a:active,
button:focus,
button:active {outline:none}

body {background-color:#EAEEF1}
.container {width:1200px; margin:50px auto}
.nav {position:fixed; top:5%; left:5%; z-index:1; border-radius:5px; background:#fff; box-shadow: 0px 5px 10px 3px rgba(0,0,0,0.2);}
.nav li {padding:10px}
.nav li ~ li {border-top:1px solid #ccc}
.nav a {font-size:14px}